import { put, takeLatest, call, all, select } from 'redux-saga/effects'
import { axios } from '../../utils/axios'
import uuid from 'uuidv4'

import {
  ShowDialog
} from '../actions'

function *GetUserList() {
  const result = yield call(axios, { method: 'get' }, 'GET_USER_LIST')
  if (result.type === 'GET_USER_LIST') {
    yield put(result)
  }
}

function *GetUserDetails({ payload }) {
  yield put({
    type: 'UPDATE_BREADCRUMBS',
    payload: {
      ...payload,
      type: 'user',
      label: `${payload.first_name} ${payload.last_name}`,
      path: '/user/'
    }
  })
}

function *CreateUser({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_USER',
      payload: [
        {
          id: uuid(),
          company: {
            name: company
          },
          ...rest
        },
        ...list
      ]
    }),
    put(ShowDialog(false))
  ])
}

function *UpdateUser({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_USER',
      payload: list.map(e => {
        if (payload.id === e.id) {
          return {
            ...e,
            ...rest,
            company: {
              ...e.company,
              name: company
            }
          }
        }
        return e
      })
    }),
    put(ShowDialog(false))
  ])
}

function *DeleteUser({ payload }) {
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_USER',
      payload: list.filter(e => e.id !== payload)
    }),
    put(ShowDialog(false))
  ])
}

export default function *() {
  yield takeLatest('GET_USER_LIST_REQUESTED', GetUserList)
  yield takeLatest('GET_USER_DETAILS_REQUESTED', GetUserDetails)
  yield takeLatest('CREATE_USER_REQUESTED', CreateUser)
  yield takeLatest('UPDATE_USER_REQUESTED', UpdateUser)
  yield takeLatest('DELETE_USER_REQUESTED', DeleteUser)
}