const data = [
  {
    id: 1,
    name: 'Organization 1',
    users: 'Quinatadcan',
    surveys: 100,
    status: 'Active',
    updated: new Date().toISOString()
  },
  {
    id: 2,
    name: 'Organization 2',
    users: 'Dwight',
    surveys: 150,
    status: 'Active',
    updated: new Date().toISOString()
  },
  {
    id: 3,
    name: 'Organization 3',
    users: 'Cogay',
    surveys: 200,
    status: 'Active',
    updated: new Date().toISOString()
  },
  ...Array(5).fill({
    id: 3,
    name: 'Organization',
    users: 'Dee',
    surveys: 250,
    status: 'Active',
    updated: new Date().toISOString()
  }).map((e, i) => ({ ...e, id: e.id + i }))
]

const INITIAL_STATE = {
  list: { data, count: data.length },
  data: null
}

const actionHandlers = {
  GET_ORGANIZATION_LIST: (state, action) => {
    return {
      ...state,
      list: action.payload
    }
  }
}

export default (state = INITIAL_STATE, action) => {
  if (actionHandlers.hasOwnProperty(action.type)) {
    return actionHandlers[action.type](state, action)
  }
  return state
}