const INITIAL_STATE = {
  list: { data, count: data.length },
  data: null,
  logout: false
}

const actionHandlers = {
  GET_USER_LIST: (state, action) => {
    return {
      ...state,
      list: action.payload
    }
  },

  USER_GRID_SORT: (state, { payload }) => {
    return {
      ...state,
      list: {
        ...state.list,
        data: _.orderBy(state.list.data, Object.keys(payload), Object.values(payload))
      }
    }
  },

  UPDATE_USER: (state, { payload }) => {
    return {
      ...state,
      list: payload
    }
  },

  LOGOUT_USER: (state, { payload }) => {
    return {
      ...state,
      logout: true,
      data: null
    }
  }
}

export default (state = INITIAL_STATE, action) => {
  if (actionHandlers.hasOwnProperty(action.type)) {
    return actionHandlers[action.type](state, action)
  }
  return state
}