const INITIAL_STATE = {
  dialog: {},
  breadCrumbs: [],
  activeData: {}
}

const actionHandlers = {
  SHOW_DIALOG: (state, action) => {
    return {
      ...state,
      dialog: {
        ...(action.payload || {})
      }
    }
  },
  UPDATE_BREADCRUMBS: (state, { payload }) => {
    const isArr = Array.isArray(payload)
    const hasType = state.breadCrumbs.find(({ type = '' }) => type === payload.type)

    return {
      ...state,
      breadCrumbs: isArr
        ? payload
        : hasType
          ? [...state.breadCrumbs]
            .map(e => e.type === payload.type ? payload : e)
          : [...state.breadCrumbs, payload],
      activeData: payload
    }
  },
  CLEAR_BREADCRUMBS: (state) => {
    return { ...state, breadCrumbs: [] }
  }
}

export default (state = INITIAL_STATE, action) => {
  if (actionHandlers.hasOwnProperty(action.type)) {
    return actionHandlers[action.type](state, action)
  }
  return state
}